* {
    box-sizing: border-box;
}

body {
    font-family: Arial, Helvetica, sans-serif;
}

.main {
    padding-bottom: 50px;
}

header .headerContainer {
    max-width: 900px;
    margin: 40px auto;
}
.container {
    display: flex;
    max-width: 900px;
    margin: auto;
    gap: 80px;
}
.container .leftSide {
    flex: 1;

}

.container .leftSide h1 {
    margin: 0;
    font-size: 40px;
    color: #3A4B5C;
}

.container .leftSide p {
    font-size: 16px;
    margin-bottom: 40px;
    color: #6A7D8B;
}

.container .leftSide input{
    width: 100%;
    border: 0;
    border-bottom: 2px solid rgba(150, 171, 171, 0.5);
    padding: 10px 2px;
    margin-bottom: 20px;
    font-size: 14px;
    outline: 0;

}

.container .leftSide input:disabled{
    opacity: 0.5;
}

.container .leftSide button{
    background-color: #227C9D;
    color: white;
    font-size: 15px;
    border: 0;
    border-radius: 10px;
    padding: 15px 0;
    width: 100%;
    cursor: pointer;
    margin-top: 40px;
    transition: all ease .3s;
}

.container .leftSide button:hover{
    opacity: .8;
}

.container .leftSide button:disabled{
    opacity: 0.5;
}

.container .rightSide {
    flex: 1;
    display: flex;
}

.container .rightSide .grid{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

.container .rightSide .rightBig{
    flex: 1;
    display: flex;
}

.container .rightSide .rightArrow {
    position: absolute;
    background-color: #227C9D;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-left: -35px;
    margin-top: 145px;
    transition: transform 0.6s ease, opacity 0.3s ease; /* Adiciona transição para o efeito */
    transform: rotateY(0deg); /* Estado inicial */
}

.container .rightSide .rightArrow:hover {
    opacity: 0.8;
    transform: rotateY(360deg); /* Gira o elemento ao passar o mouse */
}

@media (max-width: 915px) {
    header {
        padding: 0 20px;
    }

    .container{
        padding: 0 20px;
        flex-direction: column;
    }

    .container .leftSide{
        margin-right: 0;
    }

    .container .rightSide{
        margin-left: 0;
        margin-top: 50px;
    }

    .container .rightSide .rightArrow{
        margin-left: 0;
        margin-top: 0;
        border-radius: 10px;
    }
}

@media (max-width: 520px) {
    .container .rightSide .grid{
        grid-template-columns: 1fr;
    }
}